html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	font-family: 'Merriweather', serif;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

//--------------------------------HEADER---------------------
.page-header {
    background: black;
    position: sticky;
    top: 0;
    z-index: 999;
    
}

.page-logo {}
.page-logo img {
    display: block;
    width: 250px;
    height: 80px;

}

.page-nav {}
.page-nav-list {
    list-style:none;
    padding:0;
    margin:0;
    display: flex;
    justify-content: space-between;
}
.page-nav-list li {}
.page-nav-list a {
    padding:15px;
    display: block;
    font-weight: bold;
    border-radius: 5px;
    font-family: sans-serif;
    text-decoration: none;
    color: white;
    transition: 0.5s;
    cursor: pointer;

    &:hover {
        background: black;
        color: #f15701;
    }
}

.nav-toggle {
    background: none;
    width: 70px;
    height:70px;
    border:0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    cursor: pointer;
    display: none;
    outline:none;

    span {
        width:40px;
        height:6px;
        background: #fff;
        display: block;
        margin: 3px auto;
        border-radius:2px;
    }
}


